import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { axiosInstance } from 'utils';
import BaseConfigPlugin from './BaseConfigPlugin';

const AdvancedParametersPlugin = (props) => {
    const [rangeOptions, setRangeOptions] = useState([]);

    useEffect(() => {
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'teamworkconfig:AdminRoleService',
                },
            })
            .then((response) => {
                setRangeOptions(response.data);
            })
            .catch((err) => {
                console.error(err);
            });
    }, []);

    return (
        <div>
            {props.fields.length > 0 ? (
                <BaseConfigPlugin fields={props.fields} updateField={props.updateField} rangeOptions={rangeOptions} />
            ) : null}
        </div>
    );
};

AdvancedParametersPlugin.propTypes = {
    updateField: PropTypes.func,
    fields: PropTypes.array,
};

export default AdvancedParametersPlugin;
