import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import TopBraidSelect from 'TopBraidSelect';
import { ShortGraphNameComponent } from './NestedForms/ShortGraphNameComponent';
import { LDAPComponent } from './NestedForms/LDAPComponent';
import { RelyingPartyComponent } from './NestedForms/RelyingPartyComponent';
import * as TBUtils from 'utils';
import { populateErrorModal } from 'swa';
import { PasswordWrapper } from 'TBComponents';
import _ from 'lodash';

const VIEWER_CONTAINER = {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
};

const BUTTON_STYLE = {
    marginLeft: '3em',
};

const LABEL_STYLE = {
    fontWeight: '600',
};

const VALUE_STYLE = {
    fontWeight: '600',
};

const FORM_STYLE = {
    background: '#fafafa',
    border: '1px solid black',
    borderRadius: '10px',
    margin: '10px',
    padding: '15px',
    flexGrow: '1',
};

const BOOLEAN_OPTIONS = [
    { value: true, label: 'True' },
    { value: false, label: 'False' },
];

const ConfigFormInput = (props) => {
    const currentProps = useRef(props);

    useEffect(() => {
        currentProps.current = props;
    });

    const STRING = 'http://www.w3.org/2001/XMLSchema#string';
    const BOOLEAN = 'http://www.w3.org/2001/XMLSchema#boolean';
    const INT = 'http://www.w3.org/2001/XMLSchema#int';
    const INTEGER = 'http://www.w3.org/2001/XMLSchema#integer';
    const LONG = 'http://www.w3.org/2001/XMLSchema#long';
    const DOUBLE = 'http://www.w3.org/2001/XMLSchema#double';
    const OAUTH = 'http://server.topbraidlive.org/web/2009/config#OAuthRelyingParty';
    const LDAP = 'http://server.topbraidlive.org/web/2009/config#JNDIConfiguration';
    const SHORT_NAMES = 'http://server.topbraidlive.org/web/2009/config#GraphName';
    const ROLES = 'http://rdfs.org/sioc/ns#Role';
    const RESOURCE = 'http://www.w3.org/2000/01/rdf-schema#Resource';

    const ICON_ID = 'edit-icon-' + currentProps.current.id;

    const [inputValue, setInputValue] = useState(currentProps.current.field.value);
    const [editing, setEditing] = useState(false);
    const inputRef = useRef(null);
    const containerRef = useRef(null);
    const statusRef = useRef(null);

    const handleEvent = (e) => {
        if (containerRef.current && !containerRef.current.contains(e.target)) {
            setInputValue(currentProps.current.field.value);
            setEditing(!editing);
        }
    };

    const handleKeyEvent = (e) => {
        if (e.key === 'Escape') {
            setEditing(!editing);
        }
        if (e.key === 'Enter') {
            let range = currentProps.current.field.configRange;
            if (range === INT || range === INTEGER || range === LONG || range === DOUBLE) {
                submitIntHandler();
            } else {
                submitHandler();
            }
        }
    };

    useEffect(() => {
        if (editing) {
            document.addEventListener('mousedown', handleEvent, false);
            document.addEventListener('keydown', handleKeyEvent, false);
        } else {
            document.removeEventListener('mousedown', handleEvent, false);
            document.removeEventListener('keydown', handleKeyEvent, false);
        }

        return () => {
            document.removeEventListener('mousedown', handleEvent, false);
            document.removeEventListener('keydown', handleKeyEvent, false);
        };
    }, [editing]);

    const getInput = (field) => {
        switch (field.configRange) {
            case STRING:
                return renderInput('text');
            case INT:
            case INTEGER:
            case LONG:
            case DOUBLE:
                return renderInput('number');
            case BOOLEAN:
                return (
                    <React.Fragment>
                        <TopBraidSelect
                            value={getSelectValue()}
                            className="w-25"
                            options={BOOLEAN_OPTIONS}
                            onChangeCallback={handleSelectChange}
                        />
                        <i ref={statusRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
                    </React.Fragment>
                );
            case 'enum':
                return (
                    <React.Fragment>
                        <TopBraidSelect
                            className="w-25"
                            value={getSelectValue()}
                            options={field.configOptions}
                            onChangeCallback={handleSelectChange}
                        />
                        <i ref={statusRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
                    </React.Fragment>
                );
            case ROLES:
            case RESOURCE:
                return (
                    <React.Fragment>
                        <TopBraidSelect
                            className="w-25"
                            value={field.selectedVal || getSelectValue()}
                            options={currentProps.current.rangeOptions}
                            onChangeCallback={handleSelectChange}
                            components={{ Option: TBUtils.CustomOption }}
                            titleKey="title"
                        />
                        <i ref={statusRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
                    </React.Fragment>
                );
            case OAUTH:
                return <RelyingPartyComponent field={field} updateData={updateData} />;
            case LDAP:
                return <LDAPComponent field={field} updateData={updateData} />;
            case SHORT_NAMES:
                return <ShortGraphNameComponent field={field} updateData={updateData} />;
            case 'SMTPPassword':
                return (
                    <PasswordWrapper
                        field={field}
                        urlProperty="http://server.topbraidlive.org/web/2009/config#smtpServer"
                        userNameProperty="http://server.topbraidlive.org/web/2009/config#mailControllerUser"
                        noDataMessage="Please provide a Server URL and Username"
                        updateData={updateData}
                    />
                );
            case 'ClientSecretPassword':
                return (
                    <PasswordWrapper
                        field={field}
                        urlProperty="http://server.topbraidlive.org/web/2009/config#oauthAuthorizationServerURL"
                        userNameProperty="http://server.topbraidlive.org/web/2009/config#oauthClientID"
                        noDataMessage="Please provide a Server URL and Client ID"
                        updateData={updateData}
                    />
                );
            case 'MauiPassword':
                return (
                    <PasswordWrapper
                        field={field}
                        urlProperty="http://server.topbraidlive.org/web/2009/config#mauiServerURL"
                        userNameProperty="http://server.topbraidlive.org/web/2009/config#mauiServerUserName"
                        noDataMessage="Please provide a Server URL and Username"
                        updateData={updateData}
                    />
                );
            case 'ExplorerPassword':
                return (
                    <PasswordWrapper
                        field={field}
                        urlProperty="http://server.topbraidlive.org/web/2009/config#explorerServer"
                        userNameProperty="http://server.topbraidlive.org/web/2009/config#explorerServerUserName"
                        noDataMessage="Please provide a Server URL and User Name"
                        updateData={updateData}
                    />
                );
            case 'EditorPassword':
                return (
                    <PasswordWrapper
                        field={field}
                        urlProperty="http://server.topbraidlive.org/web/2009/config#editorServer"
                        userNameProperty="http://server.topbraidlive.org/web/2009/config#editorServerUserName"
                        noDataMessage="Please provide an Editor URL and User Name"
                        updateData={updateData}
                    />
                );
            case 'JIRAPassword':
                return (
                    <PasswordWrapper
                        field={field}
                        urlProperty="http://server.topbraidlive.org/web/2009/config#jiraURL"
                        userNameProperty="http://server.topbraidlive.org/web/2009/config#jiraUser"
                        noDataMessage="Please provide a Server URL and User Name"
                        updateData={updateData}
                    />
                );
            case 'Password':
                if (field.configURLProperty && field.configNameProperty) {
                    return (
                        <PasswordWrapper
                            field={field}
                            urlProperty={field.configURLProperty}
                            userNameProperty={field.configNameProperty}
                            noDataMessage={field.configMessage}
                            updateData={updateData}
                        />
                    );
                }
                break;
            default:
                return renderInput('text');
        }
    };

    const updateData = (uri, value) => {
        currentProps.current.updateField(uri, value);

        if (currentProps.current.field.configRange.includes('Password')) {
            setEditing(!editing);
        }
    };

    const renderInput = (type) => {
        let handler = type === 'text' ? submitHandler : submitIntHandler;
        return (
            <React.Fragment>
                <input
                    autoFocus
                    ref={inputRef}
                    onChange={updateValue}
                    value={getValue()}
                    className="form-control"
                    type={type}
                ></input>
                <button
                    disabled={inputValue == null}
                    style={BUTTON_STYLE}
                    className="btn btn-primary btn-sm"
                    onClick={handler}
                >
                    Submit
                </button>
                <i ref={statusRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
            </React.Fragment>
        );
    };

    const handleSelectChange = (value, { action }) => {
        let params = null;

        if (statusRef.current) {
            statusRef.current.classList.remove('d-none');
        }

        if (action === 'clear') {
            params = new URLSearchParams({
                _snippet: true,
                _viewClass: 'tbladmin:RemoveConfigValue',
                configOption: currentProps.current.field.configItem,
            });
        } else {
            let _viewClass = 'tbladmin:SetStringConfigValueService';

            if (currentProps.current.field.configRange === RESOURCE) {
                _viewClass = `tbladmin:SetResourceConfigValueService`;
            }

            if (currentProps.current.field.configRange === BOOLEAN) {
                _viewClass = 'tbladmin:SetBooleanConfigValueService';
            }

            params = new URLSearchParams({
                _snippet: true,
                _viewClass: _viewClass,
                param: currentProps.current.field.configItem,
                value: value.value,
            });
        }

        TBUtils.axiosInstance
            .post('swp', params)
            .then(() => {
                statusRef.current.classList.add('d-none');
                let returnVal = value == null ? value : value.value;

                updateData(currentProps.current.field.configItem, value);

                setInputValue(returnVal);
                setEditing(!editing);
            })
            .catch((err) => {
                populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
            });
    };

    const getSelectValue = () => {
        if (
            currentProps.current.field != null &&
            currentProps.current.field.value != null &&
            _.isObject(currentProps.current.field.value)
        ) {
            return currentProps.current.field.value;
        }

        if (inputValue == null) {
            return inputValue;
        }

        return {
            value: inputValue,
            label: `${inputValue}`,
        };
    };

    const updateValue = (event) => {
        setInputValue(event.currentTarget.value);
    };

    const submitHandler = () => {
        if (inputRef.current !== null) {
            statusRef.current.classList.remove('d-none');
            let value = inputRef.current.value;

            let params = new URLSearchParams({
                _snippet: true,
                _viewClass: 'tbladmin:SetStringConfigValueService',
                param: currentProps.current.field.configItem,
                value: value,
            });

            TBUtils.axiosInstance
                .post('swp', params)
                .then((response) => {
                    if (statusRef.current !== null) {
                        statusRef.current.classList.add('d-none');
                    }
                    if (response.data.result == 'ok') {
                        updateData(currentProps.current.field.configItem, value);
                        setEditing(!editing);
                    }
                })
                .catch((err) => {
                    populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    };

    const submitIntHandler = () => {
        statusRef.current.classList.remove('d-none');
        let value = parseInt(inputRef.current.value);

        if (_.isInteger(value)) {
            let params = new URLSearchParams({
                _snippet: true,
                _viewClass: 'tbladmin:SetIntegerConfigValueService',
                param: currentProps.current.field.configItem,
                value: value,
            });

            TBUtils.axiosInstance
                .post('swp', params)
                .then((response) => {
                    statusRef.current.classList.add('d-none');
                    if (response.data.result == 'ok') {
                        updateData(currentProps.current.field.configItem, value);
                        setEditing(!editing);
                    }
                })
                .catch((err) => {
                    populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
                });
        } else {
            populateErrorModal('This input requires a numeric value');
        }
    };

    const showPlaceholder = () => {
        return <i className="text-muted">Click to update</i>;
    };

    const getValue = () => {
        if (inputValue == null) {
            return inputValue;
        }

        if (currentProps.current.field && currentProps.current.field.configRange.includes('Password')) {
            return currentProps.current.field.value === true ? '****' : null;
        }

        if (
            currentProps.current.field &&
            currentProps.current.field.value &&
            _.isPlainObject(currentProps.current.field.value)
        ) {
            return currentProps.current.field.value.label;
        }

        if (_.isArray(inputValue)) {
            if (inputValue.length == 0) {
                return showPlaceholder();
            } else {
                return (
                    <React.Fragment>
                        {inputValue.map((val, idx) => {
                            if (Array.isArray(val.data)) {
                                return (
                                    <div style={FORM_STYLE} key={`cfg_${val.bnodeId}_${idx}`}>
                                        {val.data.map((el, elIdx) => {
                                            return (
                                                <div key={`cfg_item_${el.configItemLabel}_${elIdx}`}>
                                                    <label style={LABEL_STYLE}>{el.configItemLabel} </label>:{' '}
                                                    {el.value != '' ? el.value : 'TBD'}
                                                </div>
                                            );
                                        })}
                                    </div>
                                );
                            }
                        })}
                    </React.Fragment>
                );
            }
        } else {
            return `${inputValue}`;
        }
    };

    const setEditMode = () => {
        setEditing(!editing);
    };

    const showEditIcon = () => {
        let element = document.getElementById(ICON_ID);
        element.classList.remove('swa-hidden');
    };

    const hideEditIcon = () => {
        let element = document.getElementById(ICON_ID);
        element.classList.add('swa-hidden');
    };

    return (
        <div className="mb-3 d-flex">
            {editing === false ? (
                <div style={VALUE_STYLE}>
                    <span
                        className="w-100"
                        onMouseEnter={showEditIcon}
                        onMouseLeave={hideEditIcon}
                        onClick={setEditMode}
                    >
                        {getValue(currentProps.current.field) || showPlaceholder()}
                        <i id={ICON_ID} className="fas fa-pen swa-hidden"></i>
                    </span>
                </div>
            ) : (
                <div style={VIEWER_CONTAINER} ref={containerRef}>
                    {getInput(currentProps.current.field)}
                </div>
            )}
        </div>
    );
};

ConfigFormInput.propTypes = {
    field: PropTypes.object,
    updateField: PropTypes.func,
};

export { ConfigFormInput };
