import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import { axiosInstance } from 'utils';
import { populateErrorModal } from 'swa';
import { NestedFormInput } from './NestedFormInput';

const shortGraphNames = [
    {
        configItem: 'http://server.topbraidlive.org/web/2009/config#graph',
        configItemLabel: 'graph',
        configRange: 'http://www.w3.org/2001/XMLSchema#string',
        value: '',
    },
    {
        configItem: 'http://server.topbraidlive.org/web/2009/config#name',
        configItemLabel: 'name',
        configRange: 'http://www.w3.org/2001/XMLSchema#string',
        value: '',
    },
];

const FORM_STYLE = {
    background: '#fafafa',
    border: '1px solid black',
    borderRadius: '10px',
    margin: '10px',
    padding: '15px',
    width: '100%',
};

const DELETE_STYLE = {
    marginTop: '15px',
    cursor: 'pointer',
};

const NestedComponent = (props) => {
    const { item } = props;
    let nestedFields = item.data;

    if (item.data && Array.isArray(item.data) && item.data.length == 0) {
        nestedFields = shortGraphNames;
    }

    const removeItem = () => {
        if (props.removeItem) {
            props.removeItem(item);
        }
    };

    const updateField = (id, path, value) => {
        if (props.updateField) {
            props.updateField(id, path, value);
        }
    };

    return (
        <div className="d-flex">
            <div style={FORM_STYLE}>
                {nestedFields.map((field, idx) => {
                    return (
                        <NestedFormInput
                            key={`nc_nfi_${idx}_${item.bnodeId}`}
                            field={field}
                            bNodeID={item.bnodeId}
                            updateField={updateField}
                            id={`${item.bnodeId}_${field.configItem}`}
                        />
                    );
                })}
            </div>
            <i
                style={DELETE_STYLE}
                className="fas fa-trash-alt"
                onClick={(e) => {
                    removeItem(e);
                }}
            ></i>
        </div>
    );
};

const ShortGraphNameComponent = (props) => {
    const { field } = props;
    const { configItem, configRange } = field;
    const statusRef = useRef(null);
    let defaultValues = [];

    if (field.value) {
        defaultValues = field.value.map((value) => {
            return {
                bnodeId: value.bnodeId,
                data: value.data,
            };
        });
    }

    const [values, setValues] = useState(defaultValues);
    const isFirstRun = useRef(true);

    useEffect(() => {
        if (isFirstRun.current) {
            isFirstRun.current = false;
            return;
        }

        if (props.updateData) {
            props.updateData(configItem, values);
        }
    }, [values]);

    const getBNode = () => {
        statusRef.current.classList.remove('d-none');
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'tbladmin:ServerConfigFormNestedData',
                    type: configRange,
                    path: configItem,
                },
            })
            .then((response) => {
                statusRef.current.classList.add('d-none');
                createNewShortGraph(response.data.id);
            })
            .catch((err) => {
                populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
            });
    };

    const createNewShortGraph = (id) => {
        let newValues = values.slice(0);

        let newFormFields = shortGraphNames.map((f) => {
            f.value = '';
            return f;
        });

        newValues.unshift({ bnodeId: id, data: newFormFields });

        setValues(newValues);
    };

    const updateShortGraph = (id, path, value) => {
        let newFormValues = values.slice(0);

        let oldTargetValue = newFormValues.filter((field) => field.bnodeId == id)[0];
        let oldTargetValueIndex = newFormValues.indexOf(oldTargetValue);

        let newTargetValue = { ...oldTargetValue };

        let oldNestedValue = newTargetValue.data.filter((valueField) => valueField.configItem == path)[0];
        let oldNestedIndex = newTargetValue.data.indexOf(oldNestedValue);

        let newNestedValue = { ...oldNestedValue };

        newNestedValue.value = value;

        newTargetValue.data.splice(oldNestedIndex, 1, newNestedValue);

        newFormValues.splice(oldTargetValueIndex, 1, newTargetValue);

        setValues(newFormValues);
    };

    const removeItem = (item) => {
        if (item && item.bnodeId) {
            let params = new URLSearchParams({
                _snippet: true,
                _viewClass: 'tbladmin:RemoveConfigValue',
                value: `<${item.bnodeId}>`,
                configOption: `<${configItem}>`,
            });

            axiosInstance
                .post('swp', params)
                .then(() => {
                    let newFormValues = values.slice(0);

                    let targetValue = newFormValues.filter((field) => field.bnodeId == item.bnodeId)[0];
                    let targetValueIndex = newFormValues.indexOf(targetValue);
                    newFormValues.splice(targetValueIndex, 1);

                    setValues(newFormValues);
                })
                .catch((err) => {
                    populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    };

    return (
        <div className="w-100">
            <div>
                <button className="btn btn-light mb-3" onClick={getBNode} type="button">
                    Add New
                </button>
                <i ref={statusRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
            </div>
            {values &&
                values.map((item, idx) => {
                    return (
                        <NestedComponent
                            item={item}
                            id={item.bnodeId.replace(/\D/g, '')}
                            key={`nfi_${idx}_${item.bnodeId}`}
                            removeItem={removeItem}
                            updateField={updateShortGraph}
                        />
                    );
                })}
        </div>
    );
};

ShortGraphNameComponent.propTypes = {
    field: PropTypes.object,
    updateField: PropTypes.func,
};

export { ShortGraphNameComponent };
