import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import { axiosInstance } from 'utils';
import { populateErrorModal, populateModalDialog, populateConfirmDialog } from 'swa';

const CREATE_CONTAINER = {
    display: 'flex',
    justifyContent: 'space-between',
};

const RESTORE_CONTAINER = {
    display: 'flex',
    justifyContent: 'space-between',
    backgroundColor: '#dfe8f6',
    margin: '15px',
    borderRadius: '5px',
    padding: '10px',
};

const LINK_STYLE = {
    fontSize: '16px',
};

const UPLOAD_CONTAINER = {
    display: 'flex',
    justifyContent: 'space-between',
    alignSelf: 'center',
};

const BACKUP_CONTAINER_LEFT = {
    display: 'inline-flex',
    justifyContent: 'flex-start',
    padding: '6px 12px',
};

const BACKUP_CONTAINER_RIGHT = {
    display: 'inline-flex',
    justifyContent: 'flex-end',
};

const SPAN_STYLE = {
    marginRight: '10px',
    fontWeight: 'normal',
    alignSelf: 'center',
    padding: '6px 12px',
};

const HEADER_STYLE = {
    fontSize: '24px',
    color: '#005a9c',
};

const SPACING_RIGHT = {
    minWidth: '220px',
};

const RESTORE_INFO = {
    flexGrow: '1',
    display: 'grid',
};

const RESTORE_LABEL = {
    fontSize: '16px',
};

const RESTORE_DATE = {
    fontSize: '12px',
};

const RESTORE_BTN = {
    margin: '5px',
};

const BUCKET_STYLE = {
    backgroundColor: '#dfe8f6',
};

const BackupRestorePage = (props) => {
    const [backups, setBackups] = useState([]);
    const [runningBackup, setRunningBackup] = useState(false);
    const [runningRestore, setRunningRestore] = useState(false);
    const [file, setFile] = useState(new File([''], ''));
    const [skipBackupPreRestart, setskipBackupPreRestart] = useState(false);
    const [skipBackupPostRestart, setskipBackupPostRestart] = useState(false);
    const [version, setVersion] = useState('');
    const [loadingText, setLoadingText] = useState('');

    useEffect(() => {
        let versionArray = document.getElementsByClassName('version')[0].innerHTML.split(':')[1].split('.');
        setVersion(versionArray[0].trim() + '.' + versionArray[1]);
        getBackups();
    }, []);

    const getBackups = () => {
        axiosInstance
            .get('getBackups')
            .then((data) => {
                orderBackups(data.data);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    const orderBackups = (backups) => {
        let orderdBackups = backups
            .sort((backupA, backupB) => {
                let _a = backupA.date;
                let _b = backupB.date;

                return _a.localeCompare(_b);
            })
            .reverse();

        setBackups(orderdBackups);
    };

    const createBackup = () => {
        if (runningBackup) {
            populateModalDialog('A backup process is currently running.');
            return;
        }

        setRunningBackup(true);

        return axiosInstance
            .post('backupProjects')
            .then((response) => {
                var result = populateModalDialog('Successfully created new backup');

                Promise.resolve(result).then(() => {
                    let newBackup = {
                        identifier: response.data.identifier,
                        date: response.data.date,
                    };

                    let newBackupsList = backups.slice(0);
                    newBackupsList.push(newBackup);
                    orderBackups(newBackupsList);
                });
            })
            .catch((err) => {
                populateErrorModal(err);
            })
            .finally(() => {
                setRunningBackup(false);
            });
    };

    const onDeleteClick = (e, item) => {
        var deleteConfirmed = swa.populateConfirmDialog('Delete backup ' + item + '?');

        return Promise.resolve(deleteConfirmed).then(() => {
            const params = new URLSearchParams();

            params.append('item', item);

            return axiosInstance
                .delete('backupUtils', { params: params })
                .then((response) => {
                    populateModalDialog('Successfully deleted backup ' + item);

                    let cloneBackupList = backups.slice(0);

                    let newBackupsList = cloneBackupList.filter((backup) => {
                        return backup.identifier != item;
                    });

                    orderBackups(newBackupsList);
                })
                .catch((err) => {
                    populateErrorModal(err);
                });
        });
    };

    const getHREF = (item) => {
        let _item = encodeURIComponent(item);

        return `backupUtils?item=${_item}`;
    };

    const onRestoreClick = (e) => {
        e.preventDefault();
        let backup = e.target.value;

        let formData = new FormData();
        formData.append('backup', backup);
        formData.append('skipBackupPreRestart', skipBackupPreRestart);
        formData.append('skipBackupPostRestart', skipBackupPostRestart);

        confirmSubmit(formData);
    };

    const isFileEmpty = () => {
        if (file && file.size) {
            return file.size === 0;
        }
        return true;
    };

    const renderRow = (el, index) => {
        let label = el.identifier.split(':')[2].split('.')[0];
        let date = el.date;
        return (
            <div style={RESTORE_CONTAINER} key={`backup_${index}_${el.identifier}`}>
                <div style={RESTORE_INFO}>
                    <span style={RESTORE_LABEL}>{label}</span>
                    <span style={RESTORE_DATE}>{date}</span>
                </div>
                <button
                    value={el.identifier}
                    style={RESTORE_BTN}
                    className="btn btn-primary form-control"
                    onClick={onRestoreClick}
                >
                    Restore
                </button>
                <a
                    style={RESTORE_BTN}
                    className="btn btn-primary form-control"
                    href={getHREF(el.identifier)}
                    target="_blank"
                    rel="noopener noreferrer"
                >
                    Download
                </a>
                <button
                    style={RESTORE_BTN}
                    className="btn btn-danger form-control"
                    onClick={(e) => onDeleteClick(e, el.identifier)}
                >
                    Delete
                </button>
            </div>
        );
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        let formData = new FormData();

        formData.append('file', file);
        formData.append('skipBackupPreRestart', skipBackupPreRestart);
        formData.append('skipBackupPostRestart', skipBackupPostRestart);

        confirmSubmit(formData);
    };

    const confirmSubmit = (formData) => {
        populateConfirmDialog('Are you sure?', 'CAUTION').then((response) => {
            axiosInstance
                .post('restoreProjects', formData, { headers: { 'Content-Type': 'multipart/form-data' } })
                .then((response) => {
                    setLoadingText('Starting restore. Please wait...');
                    setRunningRestore(true);
                    notifyRestoreReady();
                })
                .catch((err) => {
                    populateErrorModal(err);
                });
        });
    };

    const handleFileChange = (e) => {
        setFile(e.target.files[0]);
    };

    const notifyRestoreReady = () => {
        populateModalDialog(
            'The application will restart momentarily.  This page will reload when the restore is complete'
        );
    };

    useEffect(() => {
        if (loadingText && loadingText.length > 0) {
            pollTimeoutWrapper();
        }
    }, [loadingText]);

    const pollTimeoutWrapper = () => {
        setTimeout(() => {
            pollForReloadComplete();
        }, 10000);
    };

    const pollForReloadComplete = () => {
        let currentText = loadingText;
        axiosInstance
            .get('')
            .then((data) => {
                window.location.reload();
                setRunningRestore(false);
            })
            .catch((err) => {
                setLoadingText(
                    currentText + '\n' + 'Application is still reloading... checking again in 10 seconds...'
                );
            });
    };

    const renderLoadingScreen = () => {
        return <pre>{loadingText}</pre>;
    };

    const renderPage = () => {
        return (
            <div>
                <div style={CREATE_CONTAINER}>
                    {props.bucket !== null ? (
                        <span style={LINK_STYLE}>
                            Currently using bucket : <span style={BUCKET_STYLE}>{props.bucketName}</span>
                        </span>
                    ) : (
                        <div>
                            <div style={LINK_STYLE}>
                                Please configure a new bucket{' '}
                                <a href="swp?_viewClass=teamworkconfig:ExternalSystemIntegrationManagerPage">here</a>
                            </div>
                            <div style={LINK_STYLE}>
                                If you have configured a bucket, you need to set it as the 'S3 Export Backup'{' '}
                                <a href="swp?_viewClass=teamworkconfig:ConfigEditor">here</a>
                            </div>
                        </div>
                    )}
                    {props.bucket !== null ? (
                        <button className="btn btn-default btn-primary pull-right" onClick={createBackup}>
                            {runningBackup == true ? <i className="fas fa-spinner fa-spin" /> : 'Create Backup'}
                        </button>
                    ) : (
                        <a className="btn btn-default btn-primary pull-right" href="backupProjects">
                            Create Local Backup
                        </a>
                    )}
                </div>
                <hr />
                <span style={HEADER_STYLE}>Restore Backup</span>
                <div>
                    <div>
                        <strong>
                            WARNING: This will bring the EDG server down and sever any existing sessions of users that
                            may currently be logged in.
                        </strong>
                    </div>
                    <br />
                    <div>
                        If any new collections have been created since the time of the backup, they will be cleared.
                    </div>
                    <br />
                    <div>
                        A backup of the current state of the workspace will generally be made available under
                        /config/restore/backup within the webapp's installation location.
                    </div>
                    <br />
                    <div>
                        A second backup of the pre-restored state of the TDB databases will be in this directory as
                        well. If the restore fails, and a version of the database existed in the system prior to the
                        restore, it will attempt to put this second backup back into place within the workspace.
                    </div>
                    <br />
                    <div>
                        If anything goes wrong with this automated process, it is recommended to manually copy the
                        Workspace&lt;timestamp&gt; folder back into the existing workspace.
                    </div>
                    <br />
                    <div>
                        This will return the TDB databases to their original, pre-restored state. Use this feature with
                        great care.
                    </div>
                    <br />
                    <div>
                        Be sure to consult the{' '}
                        <a href={'https://doc.topquadrant.com/' + version + '/backup-and-restore/'}>
                            product documentation page
                        </a>{' '}
                        for backup and restore before using this.
                    </div>
                </div>
                <hr />
                <div>
                    <div>
                        <br />
                    </div>
                    <form id="theForm" method="post" action="restoreProjects" enctype="multipart/form-data">
                        <div style={UPLOAD_CONTAINER} className="form-group">
                            <div style={BACKUP_CONTAINER_LEFT}>
                                Select skip first backup if you do not need to take a backup prior to the restore
                            </div>

                            <div style={BACKUP_CONTAINER_RIGHT}>
                                <div style={SPAN_STYLE} htmlFor="localUpload">
                                    Upload local backup file
                                </div>
                                <input
                                    style={SPAN_STYLE}
                                    onChange={handleFileChange}
                                    type="file"
                                    className="form-control-file"
                                    name="file"
                                    id="localUpload"
                                />
                                <button
                                    style={RESTORE_BTN}
                                    disabled={isFileEmpty()}
                                    onClick={handleSubmit}
                                    className="btn btn-primary form-control"
                                >
                                    Restore
                                </button>
                            </div>
                        </div>
                        <div>
                            <div style={UPLOAD_CONTAINER}>
                                <div style={BACKUP_CONTAINER_LEFT}>
                                    Select skip second backup&nbsp;<strong> only </strong>&nbsp;if you have an error
                                    upon restore with insufficient memory allocation
                                </div>
                                <div style={BACKUP_CONTAINER_RIGHT}>
                                    <div style={SPAN_STYLE}>Skip First Backup</div>
                                    <input
                                        class="mr-2"
                                        style={SPAN_STYLE}
                                        onChange={() => {
                                            setskipBackupPreRestart(!skipBackupPreRestart);
                                        }}
                                        type="checkbox"
                                        id="skipBackupPostRestart"
                                        name="skipBackupPostRestart"
                                        value={skipBackupPreRestart}
                                        label="Skip Second Backup"
                                    />
                                    <div style={SPAN_STYLE} class="mr-2">
                                        Skip Second Backup&nbsp;&nbsp;
                                    </div>
                                    <input
                                        style={SPAN_STYLE}
                                        class="mr-2"
                                        onChange={() => {
                                            setskipBackupPostRestart(!skipBackupPostRestart);
                                        }}
                                        type="checkbox"
                                        id="skipBackupPostRestart"
                                        name="skipBackupPostRestart"
                                        value={skipBackupPostRestart}
                                        label="Skip Second Backup"
                                    />
                                    <div style={SPACING_RIGHT} />
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
                <div>
                    {backups.map((el, index) => {
                        return renderRow(el, index);
                    })}
                </div>
            </div>
        );
    };

    return runningRestore ? renderLoadingScreen() : renderPage();
};

document.addEventListener('readystatechange', (event) => {
    if (event.target.readyState === 'interactive') {
        let element = document.querySelector('#backupRestorePage');

        if (element) {
            let bucket = element.getAttribute('data-bucket');
            let bucketName = element.getAttribute('data-bucketName');
            let authId = element.getAttribute('data-authId');
            let authType = element.getAttribute('data-authType');
            let folder = element.getAttribute('data-folder');
            let region = element.getAttribute('data-region');
            let proxyConfig = element.getAttribute('data-proxyConfig');

            ReactDOM.render(
                <BackupRestorePage
                    authId={authId}
                    authType={authType}
                    folder={folder}
                    region={region}
                    proxyConfig={proxyConfig}
                    bucket={bucket}
                    bucketName={bucketName}
                />,
                element
            );
        }
    }
});
