import AdvancedParametersPlugin from './plugins/AdvancedParametersPlugin';
import AutoClassifierConfigurationParametersPlugin from './plugins/AutoClassifierConfigurationParametersPlugin';
import S3ConfigurationParametersPlugin from './plugins/S3ConfigurationParametersPlugin';
import MigrationsPlugin from './plugins/MigrationsPlugin';

let registry = {};

/**
 * Register React Components with the ViewRegistry
 * these Components can then be utilized to replace componentry in EDG config.
 *
 * @param {*} key
 * @param {*} component
 */
export const registerPlugin = (key, component) => {
    registry[key] = component;
};

export const getPlugin = (key) => {
    if (Object.hasOwnProperty.call(registry, key)) {
        return registry[key];
    }

    return null;
};

export const getAllPlugins = () => {
    return registry;
};

registerPlugin('http://server.topbraidlive.org/system-applications/tbladmin#S3Config', S3ConfigurationParametersPlugin);
registerPlugin(
    'http://server.topbraidlive.org/system-applications/tbladmin#AdvancedParameters',
    AdvancedParametersPlugin
);
registerPlugin('http://server.topbraidlive.org/system-applications/tbladmin#MigrationsConfig', MigrationsPlugin);

registerPlugin(
    'http://server.topbraidlive.org/system-applications/tbladmin#AutoClassifierConfig',
    AutoClassifierConfigurationParametersPlugin
);
