import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import DatePicker from 'react-datepicker';
import { isValid, parseISO, format, isAfter, isBefore } from 'date-fns';
import { VariableSizeList as List } from 'react-window';
import AutoSizer from 'react-virtualized-auto-sizer';
import TopBraidSelect from 'TopBraidSelect';
import TimeInput from 'TimeInput';
import TimeSelector from 'TimeSelector';
import { browserSupportsTime } from 'utils';

const LogStreamerContainer = {
    whiteSpace: 'pre',
    overflow: 'hidden',
    wordWrap: 'normal',
    height: 'calc(100vh - 155px)',
};

const LIST_CONTAINER = {
    height: 'calc(100vh - 275px)',
};

const FiltersContainer = {
    borderBottom: '1px solid black',
    display: 'grid',
    gridTemplateColumns: 'auto auto',
    padding: '5px 0px',
    maxHeight: '120px',
    overflow: 'auto',
};

const FiltersStyle = {
    display: 'flex',
    alignItems: 'baseline',
    margin: '5px 15px',
};

const LOG_LEVEL_STYLE = {
    margin: '0 5px',
};

const CBOX_CONTAINER = {
    justifySelf: 'end',
    alignSelf: 'end',
};

const CBOX_STYLE = {
    marginLeft: '5px',
};

const LabelStyle = {
    marginRight: '15px',
};

const LogStreamer = () => {
    const DISABLED_MSG = 'Filters are disabled until all data is finished loading';
    const DISABLED_TIME_MSG = 'Time inputs are disabled until a date has been selected';
    const DROPDOWN_MSG = 'Use this select menu to filter logs by levels';
    const DATEPICKER_MSG = 'Use this datepicker to filter logs by date';
    const TIMEPICKER_MSG = 'Use this time input to filter logs by time';
    const LogLevels = [
        { value: 'INFO', label: 'Info' },
        { value: 'WARN', label: 'Warn' },
        { value: 'ERROR', label: 'Error' },
        { value: 'FATAL', label: 'Fatal' },
        { value: 'DEBUG', label: 'Debug' },
        { value: 'TRACE', label: 'Trace' },
    ];
    const listRef = useRef();
    const [logs, setLogs] = useState([]);
    const [timeFilter, setTimeFilter] = useState({
        minTime: '',
        maxTime: '',
        isFilteredByTime: false,
    });
    const [dateFilter, setDateFilter] = useState({ date: null, isFilteredByDate: false });
    const [filteredLogs, setFilteredLogs] = useState([]);
    const [dataLoadComplete, setDataLoadComplete] = useState(false);
    const [useColors, setUseColors] = useState(true);
    const [levelFilters, setLevelFilters] = useState({
        filters: [],
        isFiltered: false,
    });

    // Get initial data
    useEffect(() => {
        if (logs.length === 0) {
            getLogs();
        }
    }, []);

    // Filter logs by checked off log levels and/or date
    useEffect(() => {
        if (levelFilters.isFiltered || dateFilter.isFilteredByDate || timeFilter.isFilteredByTime) {
            let newFilteredLogs = logs.filter((log) => {
                if (log != null) {
                    let splitString = log.split(' ');
                    let logLevel = splitString[2];
                    let logTime = splitString[0].concat(' ').concat(splitString[1]);
                    let logDate = splitString[0];

                    let matchesLogLevel = true;
                    if (levelFilters && levelFilters.filters && levelFilters.filters.length > 0) {
                        let logFilters = levelFilters.filters.map((filter) => filter.value);
                        matchesLogLevel = logFilters.includes(logLevel);
                    }

                    let matchesDate = true;
                    if (dateFilter && dateFilter.date) {
                        matchesDate = logDate === dateFilter.date;
                    }

                    let matchesTime = true;
                    if (dateFilter && dateFilter.date && timeFilter && timeFilter.isFilteredByTime) {
                        let min = timeFilter.minTime;
                        let max = timeFilter.maxTime;

                        if (min !== '' && min !== ':00:00' && (max === '' || max === ':00:00')) {
                            let filterTime = dateFilter.date.concat(' ').concat(min);
                            matchesTime = isAfter(parseISO(logTime), parseISO(filterTime));
                        }
                        if ((min === '' || min === ':00:00') && max !== '' && max !== ':00:00') {
                            let filterTime = dateFilter.date.concat(' ').concat(max);
                            matchesTime = isBefore(parseISO(logTime), parseISO(filterTime));
                        }
                        if (min !== '' && min !== ':00:00' && max !== '' && max !== ':00:00') {
                            let filterMinTime = dateFilter.date.concat(' ').concat(min);
                            let filterMaxTime = dateFilter.date.concat(' ').concat(max);

                            matchesTime =
                                isAfter(parseISO(logTime), parseISO(filterMinTime)) &&
                                isBefore(parseISO(logTime), parseISO(filterMaxTime));
                        }
                    }
                    return matchesLogLevel && matchesDate && matchesTime;
                }
            });
            setFilteredLogs(newFilteredLogs);
            resetAfterIndex();
        }
    }, [levelFilters, dateFilter, timeFilter]);

    const getLogs = () => {
        let templogs = [];
        fetch('admin/logstream')
            .then((response) => response.body)
            .then((rs) => {
                const reader = rs.getReader();

                return new ReadableStream({
                    async start(controller) {
                        while (true) {
                            const { done, value } = await reader.read();
                            if (done) {
                                setDataLoadComplete(true);
                                resetAfterIndex();
                                break;
                            }
                            controller.enqueue(value);

                            let newLogs = new TextDecoder('utf-8').decode(value);
                            let logRows = splitLogs(newLogs);
                            templogs = templogs.concat(logRows);
                            setLogs(templogs);
                        }
                        // Close the stream
                        controller.close();
                        reader.releaseLock();
                    },
                });
            })
            .catch(console.error);
    };

    const splitLogs = (unsortedLogs) => {
        let encodedString = unsortedLogs.replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&');
        let splitLogs = '';
        if (encodedString != null) {
            splitLogs = encodedString.split('\n');
        }

        let returnLogs = [];
        for (let i = 0; i <= splitLogs.length; i++) {
            if (splitLogs[i + 1] !== undefined && splitLogs[i + 1].length > 0) {
                let nextString = '';
                if (splitLogs[i + 1] != null) {
                    nextString = splitLogs[i + 1].split(' ');
                }
                let nextRow = nextString[0];
                if (isValid(new Date(nextRow))) {
                    returnLogs.push(splitLogs[i]);
                } else {
                    let sTrace = true;
                    let newIdx = i + 1;
                    let count = 0;
                    let str = splitLogs[i];
                    while (sTrace) {
                        str = str.concat('\n').concat(splitLogs[newIdx]);
                        newIdx++;
                        count++;

                        let nextStr = '';
                        if (splitLogs[newIdx] != null) {
                            nextStr = splitLogs[newIdx].split(' ');
                        }

                        let nextRw = nextStr[0];
                        if (nextRw == null || isValid(new Date(nextRw))) {
                            sTrace = false;
                        }
                    }
                    i += count;
                    returnLogs.push(str);
                }
            } else {
                if (splitLogs[i] != undefined && splitLogs[i].length > 0) {
                    returnLogs.push(splitLogs[i]);
                }
            }
        }
        return returnLogs;
    };

    const textColor = (el) => {
        if (el !== undefined && el.length > 0) {
            let splitString = '';
            if (el != null) {
                splitString = el.split(' ');
            }
            let logLevel = splitString[2];
            switch (logLevel) {
                case 'INFO':
                    return 'green';
                    break;
                case 'WARN':
                    return 'orange';
                    break;
                case 'ERROR':
                case 'FATAL':
                    return 'red';
                    break;
                case 'DEBUG':
                case 'TRACE':
                    return 'blue';
                    break;
                default:
                    return 'black';
            }
        } else {
            return 'black';
        }
    };

    const resetAfterIndex = () => {
        listRef.current.resetAfterIndex(0, false);
    };

    const onMinTimeChange = (value) => {
        setTimeFilter((state) => ({ ...state, minTime: value, isFilteredByTime: value !== null ? true : false }));
        resetAfterIndex();
    };

    const onMaxTimeChange = (value) => {
        setTimeFilter((state) => ({ ...state, maxTime: value, isFilteredByTime: value !== null ? true : false }));
        resetAfterIndex();
    };

    const onDateChange = (value) => {
        let str = value ? format(value, 'yyyy-MM-dd') : null;
        setDateFilter({ date: str, isFilteredByDate: str !== null ? true : false });
        resetAfterIndex();
    };

    const handleFilterChange = (value) => {
        setLevelFilters({
            filters: value,
            isFiltered: value != null && value.length === 0 ? false : true,
        });
        resetAfterIndex();
    };

    const renderDateTimeFilters = () => {
        let date = dateFilter.date ? parseISO(dateFilter.date) : null;
        return (
            <div title={dataLoadComplete ? DATEPICKER_MSG : DISABLED_MSG} style={FiltersStyle}>
                <h4 style={LabelStyle}>Date:</h4>
                <DatePicker
                    className="LogsDatePicker form-control"
                    dateFormat="yyyy-MM-dd"
                    isClearable
                    onChange={onDateChange}
                    selected={isValid(date) ? date : null}
                    disabled={!dataLoadComplete}
                    portalId="datepicker-portal"
                />
            </div>
        );
    };

    const renderTimeFilters = () => {
        return (
            <div
                className="timeFilterContainer"
                title={dateFilter.isFilteredByDate ? TIMEPICKER_MSG : DISABLED_TIME_MSG}
                style={FiltersStyle}
            >
                <h4 style={LabelStyle}>Time:</h4>
                {renderTimeComponent(onMinTimeChange, timeFilter.minTime)}
                {renderTimeComponent(onMaxTimeChange, timeFilter.maxTime)}
            </div>
        );
    };

    const renderTimeComponent = (handler, time) => {
        return (
            <div style={FiltersStyle}>
                {browserSupportsTime() ? (
                    <TimeInput
                        value={time}
                        onChange={handler}
                        className="react-datepicker-time__input"
                        time={time}
                        disabled={!dateFilter.isFilteredByDate}
                    />
                ) : (
                    <TimeSelector time={time} onChange={handler} disabled={!dateFilter.isFilteredByDate} />
                )}
            </div>
        );
    };

    const renderLogLevelFilter = () => {
        return (
            <div title={dataLoadComplete ? DROPDOWN_MSG : DISABLED_MSG} style={LOG_LEVEL_STYLE}>
                <TopBraidSelect
                    menuPortalTarget={document.body}
                    value={levelFilters.filters}
                    multi={true}
                    placeholder="Log levels.."
                    onChangeCallback={handleFilterChange}
                    options={LogLevels}
                    disabled={!dataLoadComplete}
                />
            </div>
        );
    };

    const renderColorsFilter = () => {
        return (
            <span style={CBOX_CONTAINER}>
                Use Colors
                <input
                    type="checkbox"
                    checked={useColors}
                    onChange={() => {
                        setUseColors(!useColors);
                    }}
                    style={CBOX_STYLE}
                />
            </span>
        );
    };

    const Row = ({ index, style }) => {
        let data = levelFilters.isFiltered || dateFilter.isFilteredByDate ? filteredLogs : logs;
        let newStyle = { ...style };
        newStyle['lineHeight'] = '14.5px';
        return (
            <div style={newStyle}>
                <span
                    style={{
                        color: useColors === true ? textColor(data[index]) : 'black',
                        fontSize: '12px',
                        fontFamily: 'monospace-logs',
                    }}
                >
                    {data[index]}
                </span>
            </div>
        );
    };

    const getItemSize = (index) => {
        let data = levelFilters.isFiltered || dateFilter.isFilteredByDate ? filteredLogs : logs;
        let lines = '';
        if (data[index] !== null) {
            lines = data[index].split('\n').length;
        }
        return lines * 16;
    };

    return (
        <div style={LogStreamerContainer}>
            <div style={FiltersContainer}>
                {renderDateTimeFilters()}
                {renderLogLevelFilter()}
                {renderTimeFilters()}
                {renderColorsFilter()}
            </div>
            <div style={LIST_CONTAINER}>
                <AutoSizer>
                    {({ height, width }) => {
                        return (
                            <List
                                className="List"
                                height={height}
                                itemCount={
                                    levelFilters.isFiltered || dateFilter.isFilteredByDate
                                        ? filteredLogs.length
                                        : logs.length
                                }
                                itemSize={getItemSize}
                                width={width}
                                ref={listRef}
                            >
                                {Row}
                            </List>
                        );
                    }}
                </AutoSizer>
            </div>
        </div>
    );
};

var renderLogStreamer = (sel) => {
    if (sel) {
        ReactDOM.render(<LogStreamer />, sel);
    }
};

document.addEventListener('readystatechange', (event) => {
    if (event.target.readyState === 'interactive') {
        let element = document.querySelector('#logStreamerID');
        if (element) {
            renderLogStreamer(element);
        }
    }
});
