import React, { useState, useEffect } from 'react';
import { axiosInstance } from 'utils';

const MauiServerConnectionCheck = (props) => {
    const [connectionData, setConnectionData] = useState({ status: '', message: '' });

    useEffect(() => {
        getStatus();
    }, [props.input]);

    const getStatus = () => {
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'autotagger:CheckConnectionService',
                },
            })
            .then((response) => {
                setConnectionData({ status: response.data.status, message: response.data.message });
            })
            .catch((err) => {
                console.error(err);
            });
    };

    return (
        <div className={connectionData.status == 'success' ? 'text-success' : 'text-danger'}>
            {connectionData.message}
        </div>
    );
};

export { MauiServerConnectionCheck };
