import React from 'react';
import PropTypes from 'prop-types';
import { ConfigFormInput } from '../ConfigFormInput';

const FIELD_ROW = {
    display: 'grid',
    gridTemplateColumns: '40% auto',
    margin: '1em 0',
};

const LABEL_STYLE = {
    fontWeight: '600',
};

const DESCRIPTION_STYLE = {
    fontSize: '10px',
    fontStyle: 'italic',
};

const BaseConfigPlugin = (props) => {
    return (
        <div>
            {props.fields && props.fields.length > 0
                ? props.fields.map((el, idx) => {
                      let trimKey = el.configItemLabel.replace(/\s/g, '');
                      return (
                          <div className="mb-2" style={FIELD_ROW} key={`tabbody_${idx}_${idx}_${trimKey}`}>
                              <div className="mr-5">
                                  <label style={LABEL_STYLE} htmlFor={el.configItem}>
                                      {el.configItemLabel} :
                                  </label>
                                  <p style={DESCRIPTION_STYLE}>{el.comment}</p>
                              </div>
                              <div id={el.configItem}>
                                  <ConfigFormInput
                                      key={`${idx}_${trimKey}`}
                                      updateField={props.updateField}
                                      rangeOptions={props.rangeOptions}
                                      id={el.configItemLabel.replace(/\s/g, '')}
                                      field={el}
                                  />
                                  {props.extraBody ? props.extraBody(el) : null}
                              </div>
                          </div>
                      );
                  })
                : null}
        </div>
    );
};

BaseConfigPlugin.propTypes = {
    updateField: PropTypes.func,
    fields: PropTypes.array,
    rangeOptions: PropTypes.array,
};

export default BaseConfigPlugin;
