import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import BaseConfigPlugin from './BaseConfigPlugin';
import { axiosInstance } from 'utils';

const S3ConfigurationParametersPlugin = (props) => {
    const [rangeOptions, setRangeOptions] = useState([]);

    useEffect(() => {
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'teamworkconfig:S3PluginDataService',
                },
            })
            .then((response) => {
                setRangeOptions(response.data.buckets);
            })
            .catch((err) => {
                console.error(err);
            });
    }, []);

    return (
        <div>
            {props.fields.length > 0 ? (
                <BaseConfigPlugin fields={props.fields} updateField={props.updateField} rangeOptions={rangeOptions} />
            ) : null}
        </div>
    );
};

S3ConfigurationParametersPlugin.propTypes = {
    updateField: PropTypes.func,
    fields: PropTypes.array,
};

export default S3ConfigurationParametersPlugin;
