let endpoint = {};

endpoint.restoreLastSavedQuery = () => {
	if (localStorage.length > 0) {
		var lastSavedQuery = null;
		var lastSavedQueryTime = 0;
		var pattern = /yasqe_uniqueId[0-9]*_queryVal/g;
		for (var i = 0; i < localStorage.length; i++) {
			var key = localStorage.key(i);
			if (key.match(pattern)) {
				var queryObject = JSON.parse(localStorage.getItem(key));
				var queryTime = queryObject.time;
				if (queryTime > lastSavedQueryTime) {
					lastSavedQueryTime = queryTime;
					lastSavedQuery = queryObject.val;
				}
			}
		}
		if (lastSavedQuery) {
			document.getElementById('query-area').value = lastSavedQuery;
		}
	}
};

export default endpoint;

$(function() {
    $('.sparql-endpoint-page #clear-query').click(function(event) {
        //var queryArea = $("textarea#query-area");
        yasqe.setValue('');
    });

    $('.sparql-endpoint-page #clear-results').click(function(event) {
        $('.yasr_results').empty();
        localStorage.removeItem('yasr_results-content_results');
    });

    $('.sparql-endpoint-page #submit-query').click(function(event) {
        yasqe.query();
    });

    $('.sparql-endpoint-page #default_graph_uri').change(function(event) {
        yasqe.options.sparql.defaultGraphs = [];
        if (this.value != '') {
            yasqe.options.sparql.defaultGraphs.push(this.value);
        }
    });

    $('.sparql-endpoint-page #include-imports').change(function(event) {
        var include_imports = yasqe.options.sparql.args.find(function(item) {
            return item.name == 'with-imports';
        });
        if (include_imports) {
            var index = yasqe.options.sparql.args.indexOf(include_imports);
            yasqe.options.sparql.args.splice(index, 1);
        }
        if (this.checked) {
            yasqe.options.sparql.args.push({ name: 'with-imports', value: this.checked });
        }
    });

    $('.sparql-endpoint-page #submit-button').click(function() {
        //chop off the part starting from the servlet name
        var last = document.URL.lastIndexOf('/');
        var queryValue = $('.sparql-endpoint-page textarea#query-area').val();
        var checkSPARQLURL = document.URL.substring(0, last) + '/checksparql';
        var datatosend = { query: queryValue };
        var URL = document.URL.substring(0, last) + '/sparql';

        //send the requeust to the check SPARQL Servlet first
        $.ajax({
            type: 'POST',
            url: checkSPARQLURL,
            data: datatosend,
            success: function(data, status, jqXHR) {
                //check if query type update and post to corresponding servlet
                if (data.queryType == 'UPDATE') {
                    //call update servlet
                    var queryValue = $('.sparql-endpoint-page textarea#query-area').val();
                    datatosend = { update: queryValue };
                    $.ajax({
                        type: 'POST',
                        url: URL,
                        data: datatosend,
                        success: function(text) {
                            alert('Update Applied');
                        },
                        error: function(data, status, jqXHR) {
                            var errorMsgTxt;
                            if (data.getResponseHeader('badRequest')) {
                                errorMsgTxt = data.getResponseHeader('badRequest');
                            } else if (data.getResponseHeader('updatesDisabled')) {
                                errorMsgTxt = data.getResponseHeader('updatesDisabled');
                            }
                            $('.sparql-endpoint-page #results-content').text(errorMsgTxt);
                        },
                    });
                } else {
                    getQueryResults(URL);
                }
            },
            error: function(data, status, jqXHR) {
                $('.sparql-endpoint-page #results-content').text(data.responseJSON.parseError);
            },
            async: false,
        });

        function getQueryResults(URL) {
            var with_imports = $('.sparql-endpoint-page #include-imports').prop('checked') ? true : false;
            var default_graph_uri = $('.sparql-endpoint-page #default_graph_uri').val();
            var format = $('.sparql-endpoint-page #format').val();
            var datatosend = {
                query: queryValue,
                'default-graph-uri': default_graph_uri,
                format: format,
                'with-imports': with_imports,
            };

            if (format == 'json') {
                $('.sparql-endpoint-page #results-content').html('');

                var tableHTML = "<table border='1'><tr>";
                $.ajax({
                    type: 'POST',
                    url: URL,
                    data: datatosend,
                    success: function(text) {
                        $.each(text.head.vars, function(i, heading) {
                            tableHTML += '<th>' + heading + '</th>';
                        });

                        tableHTML += '</tr>';
                        $.each(text.results.bindings, function(i, val) {
                            tableHTML += '<tr>';
                            $.each(text.head.vars, function(i, heading) {
                                var v = val[heading];
                                tableHTML += '<td>' + (v ? v.value : '') + '</td>';
                            });
                            tableHTML += '</tr>';
                        });

                        tableHTML += '</table>';
                        $('.sparql-endpoint-page #results-content').append(tableHTML);
                    },
                });
                return false;
            } else {
                datatosend =
                    '?query=' +
                    encodeURIComponent(queryValue) +
                    '&default-graph-uri=' +
                    encodeURIComponent(default_graph_uri) +
                    '&format=' +
                    encodeURIComponent(format);
                if (with_imports) {
                    datatosend += '&with-imports=true';
                }
                window.open(URL + datatosend, '_blank');
            }
        }
    });
});
