import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import { axiosInstance } from 'utils';
import { populateErrorModal } from 'swa';

const BUTTON_STYLE = {
    marginLeft: '3em',
};

const LABEL_STYLE = {
    fontWeight: '600',
};

const CONTAINER = {
    height: '90px',
};

let currentString = '';

const UserInterfaceConfigForm = (props) => {
    const ICON_ID = 'lang-edit-icon';
    const [inputValue, setInputValue] = useState(props.custom);
    const [editing, setEditing] = useState(false);
    const inputRef = useRef(null);
    const containerRef = useRef(null);
    const statusRef = useRef(null);

    const handleEvent = (e) => {
        if (containerRef.current && !containerRef.current.contains(e.target)) {
            let currVal = currentString !== '' ? currentString : inputValue;
            setInputValue(currVal);
            setEditing(!editing);
        }
    };

    const handleKeyEvent = (e) => {
        if (e.key === 'Escape') {
            setEditing(!editing);
        }
    };

    const setEditMode = () => {
        setEditing(!editing);
    };

    const showEditIcon = () => {
        let element = document.getElementById(ICON_ID);
        element.classList.remove('swa-hidden');
    };

    const hideEditIcon = () => {
        let element = document.getElementById(ICON_ID);
        element.classList.add('swa-hidden');
    };

    useEffect(() => {
        if (editing) {
            document.addEventListener('mousedown', handleEvent, true);
            document.addEventListener('keydown', handleKeyEvent, true);
        } else {
            document.removeEventListener('mousedown', handleEvent, true);
            document.removeEventListener('keydown', handleKeyEvent, true);
        }

        return () => {
            document.removeEventListener('mousedown', handleEvent, false);
            document.removeEventListener('keydown', handleKeyEvent, true);
        };
    }, [editing]);

    const updateValue = (event) => {
        setInputValue(event.currentTarget.value);
    };

    const showPlaceholder = () => {
        return <i className="text-muted">Click to update</i>;
    };

    const submitHandler = () => {
        statusRef.current.classList.remove('d-none');
        let value = inputRef.current.value;
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'tbladmin:SetCustomLanguages',
                    value: value,
                },
            })
            .then((response) => {
                statusRef.current.classList.add('d-none');
                if (response.data.result == 'ok') {
                    currentString = value;
                    setEditing(!editing);
                }
            })
            .catch((err) => {
                populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
            });
    };

    return (
        <div className="m-3" style={CONTAINER}>
            <span>
                {`The custom languages setting must contain a space-separated list of ISO country codes such as "de en
                fr", overloading the default languages: "${props.defaults}" `}
            </span>
            <div className="d-flex mt-3 align-items-center">
                <label
                    title="Can be used to override the default languages in a TBL installation. Value format is the same as swa:defaultLanguages."
                    style={LABEL_STYLE}
                    htmlFor="langInput"
                    className="mr-3"
                >
                    Custom Languages :
                </label>
                {editing === false ? (
                    <React.Fragment>
                        <span
                            className="mb-2"
                            onMouseEnter={showEditIcon}
                            onMouseLeave={hideEditIcon}
                            onClick={setEditMode}
                        >
                            {inputValue == null ? showPlaceholder() : inputValue}
                        </span>
                        <i id={ICON_ID} className="fas fa-pen swa-hidden"></i>
                    </React.Fragment>
                ) : (
                    <div ref={containerRef} className="d-flex w-75">
                        <input
                            id="langInput"
                            ref={inputRef}
                            onChange={updateValue}
                            value={inputValue}
                            className="form-control w-100"
                        ></input>

                        <button
                            disabled={inputValue == null}
                            style={BUTTON_STYLE}
                            className="btn btn-primary btn-sm"
                            onClick={submitHandler}
                        >
                            Submit
                        </button>
                        <i ref={statusRef} className="fas fa-spinner fa-pulse ml-3 d-none"></i>
                    </div>
                )}
            </div>
        </div>
    );
};

document.addEventListener('readystatechange', (event) => {
    if (event.target.readyState === 'interactive') {
        let element = document.querySelector('#UserInterfaceConfigID');
        if (element != null) {
            let custom = element.getAttribute('data-custom');
            let defaults = element.getAttribute('data-default');
            if (element) {
                ReactDOM.render(<UserInterfaceConfigForm custom={custom} defaults={defaults} />, element);
            }
        }
    }
});
