import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { axiosInstance } from 'utils';
import { populateErrorModal } from 'swa';
import { PasswordWrapper } from 'TBComponents';

const BUTTON_STYLE = {
    marginLeft: '3em',
};

const LABEL_STYLE = {
    fontWeight: '600',
};

const CONTAINER_STYLE = {
    display: 'grid',
    gridTemplateColumns: '45% auto',
};

const NestedFormInput = (props) => {
    const { field } = props;
    const ICON_ID = 'edit-icon-' + props.id;
    const [inputValue, setInputValue] = useState(field ? field.value : undefined);
    const [editing, setEditing] = useState(false);
    const inputRef = useRef(null);
    const containerRef = useRef(null);
    const statusRef = useRef(null);

    const handleEvent = (e) => {
        if (containerRef.current && !containerRef.current.contains(e.target)) {
            setInputValue(field.value);
            setEditing(!editing);
        }
    };

    const handleKeyEvent = (e) => {
        if (e.key === 'Escape') {
            setEditing(!editing);
        }
        if (e.key === 'Enter') {
            submitHandler();
        }
    };

    useEffect(() => {
        if (editing) {
            document.addEventListener('mousedown', handleEvent, true);
            document.addEventListener('keydown', handleKeyEvent, true);
        } else {
            document.removeEventListener('mousedown', handleEvent, true);
            document.removeEventListener('keydown', handleKeyEvent, true);
        }

        return () => {
            document.removeEventListener('mousedown', handleEvent, false);
            document.removeEventListener('keydown', handleKeyEvent, true);
        };
    }, [editing]);

    const updateValue = (event) => {
        setInputValue(event.currentTarget.value);
    };

    const getValue = () => {
        if (inputValue == null) {
            return inputValue;
        }

        if (field.configRange.includes('Password')) {
            return field.value === true ? '****' : null;
        }

        return `${inputValue}`;
    };

    const submitHandler = () => {
        if (inputRef.current !== null) {
            statusRef.current.classList.remove('d-none');
            let value = inputRef.current.value;

            let params = new URLSearchParams({
                _snippet: true,
                _viewClass: 'tbladmin:SetStringConfigValueService',
                param: `<${field.configItem}>`,
                value: value,
                config: `<${props.bNodeID}>`,
            });

            axiosInstance
                .post('swp', params)
                .then(() => {
                    if (statusRef.current !== null) {
                        statusRef.current.classList.add('d-none');
                    }
                    if (props.updateField) {
                        props.updateField(props.bNodeID, field.configItem, value);
                    }
                    setEditing(!editing);
                })
                .catch((err) => {
                    populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    };

    const setEditMode = () => {
        setEditing(!editing);
    };

    const showEditIcon = () => {
        let element = document.getElementById(ICON_ID);
        element.classList.remove('swa-hidden');
    };

    const hideEditIcon = () => {
        let element = document.getElementById(ICON_ID);
        element.classList.add('swa-hidden');
    };

    const showPlaceholder = () => {
        return <i className="text-muted">Click to update</i>;
    };

    return (
        <div style={CONTAINER_STYLE} className="mb-3">
            <label className="mr-3" style={LABEL_STYLE}>
                {field ? field.configItemLabel : ''} :
            </label>
            {editing === false ? (
                <span onMouseEnter={showEditIcon} onMouseLeave={hideEditIcon} onClick={setEditMode}>
                    {getValue() || showPlaceholder()} <i id={ICON_ID} className="fas fa-pen swa-hidden"></i>
                </span>
            ) : (
                <React.Fragment>
                    {field.configRange == 'LDAPPassword' ? (
                        <div className="d-flex" ref={containerRef}>
                            <PasswordWrapper
                                bNode={props.bNodeID}
                                field={field}
                                config={`<${props.bNodeID}>`}
                                urlProperty="http://server.topbraidlive.org/web/2009/config#jndiurl"
                                userNameProperty="http://server.topbraidlive.org/web/2009/config#jndiusername"
                                noDataMessage="Please provide a Connection URL and Username"
                                testFunction={props.testFunction}
                                updateData={props.updateField}
                            />
                        </div>
                    ) : (
                        <div className="d-flex" ref={containerRef}>
                            <input
                                autoFocus
                                ref={inputRef}
                                onChange={updateValue}
                                value={getValue()}
                                className="form-control"
                            ></input>
                            <button style={BUTTON_STYLE} className="btn btn-primary btn-sm" onClick={submitHandler}>
                                Submit
                            </button>
                            <i ref={statusRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
                        </div>
                    )}
                </React.Fragment>
            )}
        </div>
    );
};

NestedFormInput.propTypes = {
    field: PropTypes.object,
    updateField: PropTypes.func,
    bNodeID: PropTypes.string,
};

export { NestedFormInput };
