import React, { useEffect, useState, useRef } from 'react';
import PropTypes from 'prop-types';
import { populateErrorModal } from 'swa';
import { axiosInstance } from 'utils';
import { NestedFormInput } from './NestedFormInput';

const relyingPartyIdentifiers = [
    {
        comment: 'The URL of a service that the OAuth client is authorized to use.',
        configItem: 'http://server.topbraidlive.org/web/2009/config#oauthRelyingPartyURI',
        configItemLabel: 'Relying party identifier',
        configRange: 'http://www.w3.org/2001/XMLSchema#string',
        value: '',
    },
];

const FORM_STYLE = {
    background: '#fafafa',
    border: '1px solid black',
    borderRadius: '10px',
    margin: '10px',
    padding: '15px',
    flexGrow: '1',
};

const DELETE_STYLE = {
    marginTop: '15px',
    cursor: 'pointer',
};

const RelyingPartyComponent = (props) => {
    const { field } = props;
    const { configItem, configRange } = field;
    const statusRef = useRef(null);
    let defaultValues = [];

    if (field.value) {
        defaultValues = field.value.map((value) => {
            return {
                bnodeId: value.bnodeId,
                data: value.data,
            };
        });
    }

    const [values, setValues] = useState(defaultValues);
    const isFirstRun = useRef(true);

    useEffect(() => {
        if (isFirstRun.current) {
            isFirstRun.current = false;
            return;
        }

        if (props.updateData) {
            props.updateData(configItem, values);
        }
    }, [values]);

    const getBNode = () => {
        statusRef.current.classList.remove('d-none');
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'tbladmin:ServerConfigFormNestedData',
                    type: configRange,
                    path: configItem,
                },
            })
            .then((response) => {
                statusRef.current.classList.add('d-none');
                let newValues = values !== null ? values.slice(0) : [];
                let newValue = [{ ...relyingPartyIdentifiers[0] }];

                newValues.unshift({ bnodeId: response.data.id, data: newValue });

                setValues(newValues);
            })
            .catch((err) => {
                populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
            });
    };

    const deleteForm = (item) => {
        if (item && item.bnodeId) {
            let params = new URLSearchParams({
                _snippet: true,
                _viewClass: 'tbladmin:RemoveConfigValue',
                value: `<${item.bnodeId}>`,
                configOption: `<${configItem}>`,
            });

            axiosInstance
                .post('swp', params)
                .then(() => {
                    let newFormValues = values.slice(0);

                    let targetValue = newFormValues.filter((field) => field.bnodeId == item.bnodeId)[0];
                    let targetValueIndex = newFormValues.indexOf(targetValue);
                    newFormValues.splice(targetValueIndex, 1);

                    setValues(newFormValues);
                })
                .catch((err) => {
                    populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    };

    const updateField = (id, path, value) => {
        let newData = values.slice(0);
        let returnData = [];

        newData.forEach((val) => {
            let tempVal = { ...val };
            if (tempVal.bnodeId == id) {
                tempVal.data[0].value = value;
            }
            returnData.push(tempVal);
        });

        setValues(returnData);
    };

    return (
        <div className="w-100">
            <div>
                <button className="btn btn-light mb-3" onClick={getBNode}>
                    Add New
                </button>
                <i ref={statusRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
            </div>
            {values &&
                values.map((el, idx) => {
                    return (
                        <div className="d-flex" key={`rpc_${idx}_${el.bnodeId}`}>
                            <div style={FORM_STYLE}>
                                <NestedFormInput
                                    updateField={updateField}
                                    id={el.bnodeId.replace(/\D/g, '')}
                                    field={{...el.data[0]}}
                                    bNodeID={el.bnodeId}
                                />
                            </div>
                            <i
                                style={DELETE_STYLE}
                                className="fas fa-trash-alt"
                                onClick={(e) => {
                                    deleteForm(el);
                                }}
                            ></i>
                        </div>
                    );
                })}
        </div>
    );
};

RelyingPartyComponent.propTypes = {
    field: PropTypes.object,
    updateField: PropTypes.func,
};

export { RelyingPartyComponent };
