/*global $, swa, cookies */
/* Scripts for the TBL UI page style */
import { axiosInstance, ejectLoginInterceptor } from 'utils';

$(function () {
    // Set up the x button that dismiss the message at the top of the page
    $('.tblui-section').on('click', 'button.dismiss-message', function () {
        $(this).closest('.tblui-section').hide();
    });
    // Set up the "Details" link for top-of-page messages that have details
    $('.tblui-section').on('click', '.toggle-message-details', function () {
        var $section = $(this).closest('.tblui-section');
        $section.find('.toggle-message-details').toggleClass('d-none');
        $section.find('.message-details').toggleClass('d-none');
    });
    // Set up the form for changing the user name on the User Login page
    $('form#new-username').submit(function () {
        cookies.setCookie('username', $('#input-username').val());
        // Reload instead of actually submitting the form
        location.reload();
        return false;
    });
    // Logout link for Basic Authentication
    $('#basic-auth-logout').click(function () {
        ejectLoginInterceptor();
        axiosInstance
            .get('purgeuser')
            .then((response) => {
                alert(
                    'Connections to any graphs you had open have been released.\nTo finish logging out, please quit and restart your browser.'
                );
            })
            .catch((err) => {
                swa.populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
            });
    });
});
