import React, { useState } from 'react';
import PropTypes from 'prop-types';
import BaseConfigPlugin from './BaseConfigPlugin';
import { MauiServerConnectionCheck } from './MauiServerConnectionCheck';

const AutoClassifierConfigurationParametersPlugin = (props) => {
    const [input, updateInput] = useState('');

    const reCheckConnection = (uri, value) => {
        props.updateField(uri, value);
        updateInput(value);
    };

    const renderExtraBody = (field) => {
        return (
            <React.Fragment>
                {field.configItem === 'http://server.topbraidlive.org/web/2009/config#mauiServerURL' ? (
                    <MauiServerConnectionCheck input={input} />
                ) : null}
            </React.Fragment>
        );
    };

    return (
        <div>
            {props.fields.length > 0 ? (
                <BaseConfigPlugin updateField={reCheckConnection} extraBody={renderExtraBody} fields={props.fields} />
            ) : null}
        </div>
    );
};

AutoClassifierConfigurationParametersPlugin.propTypes = {
    updateField: PropTypes.func,
    fields: PropTypes.array,
};

export default AutoClassifierConfigurationParametersPlugin;
