import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import { axiosInstance } from 'utils';
import { getAllPlugins } from './configRegistry';
import BaseConfigPlugin from './plugins/BaseConfigPlugin';

const NAV_STYLE = {
    minWidth: '225px',
};

const OVERFLOW = {
    overflowX: 'auto',
};

const ServerConfigForm = (props) => {
    const GROUPS = getAllPlugins();
    const OAUTH = 'http://server.topbraidlive.org/web/2009/config#OAuthRelyingParty';
    const LDAP = 'http://server.topbraidlive.org/web/2009/config#JNDIConfiguration';
    const SHORT_NAMES = 'http://server.topbraidlive.org/web/2009/config#GraphName';
    const [configFields, setConfigfields] = useState([]);

    useEffect(() => {
        buildFormData();
    }, []);

    const buildFormData = () => {
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'tbladmin:ServerConfigFormData',
                },
            })
            .then((response) => {
                // let configValues = [];
                // response.data.forEach((el) => {
                //     configValues.push(isNested(el));
                // });
                setConfigfields(response.data.serverConfig);
            })
            .catch((err) => {
                console.error(err);
            });
    };

    const isNested = (merged) => {
        let tempMerge = { ...merged };
        let baseData = null;
        if (tempMerge.configRange == SHORT_NAMES) {
            baseData = [...shortGraphNames];
        }
        if (tempMerge.configRange == OAUTH) {
            baseData = [...relyingPartyIdentifiers];
        }
        if (tempMerge.configRange == LDAP) {
            baseData = [...LDAPconfigFields];
        }

        if (baseData != null) {
            if (tempMerge.value != null) {
                tempMerge.value.forEach((val) => {
                    if (val.data != null && val.data.length > 0) {
                        let updatedData = baseData.map((graph) => {
                            let returnGraph = { ...graph };
                            val.data.forEach((data) => {
                                if (data.configItem === returnGraph.configItem) {
                                    returnGraph.value = data.value;
                                }
                            });
                            return returnGraph;
                        });
                        val.data = updatedData;
                    } else {
                        val.data = baseData.map((data) => Object.assign({}, data));
                    }
                });
            }
        }
        return tempMerge;
    };

    const updateField = (uri, value) => {
        let updatedValues = configFields.map((data) => data);

        updatedValues.forEach((group) => {
            if (group.fields) {
                group.fields.forEach((field) => {
                    if (field.configItem === uri) {
                        field.value = value;
                    }
                });
            }
        });

        setConfigfields(updatedValues);
    };

    return (
        <div className="m-3 d-flex">
            {configFields.length > 0 ? (
                <React.Fragment>
                    <ul style={NAV_STYLE} className="nav flex-column nav-pills mb-3" id="pills-tab" role="tablist">
                        {configFields.map((group, idx) => {
                            let trimKey = group.label.replace(/\s/g, '');
                            return (
                                <React.Fragment key={`fragement_${idx}_${trimKey}`}>
                                    {(group.label == 'Explorer Configuration' && props.isExplorer === false) ||
                                    (group.label == 'Autoclassifier Configuration' && props.isAC === false) ? null : (
                                        <li className="nav-item mb-2" role="presentation" key={`tab_${idx}_${trimKey}`}>
                                            <a
                                                className={`nav-link ${idx == 0 ? 'active' : ''}`}
                                                id={`pills-${trimKey}-tab`}
                                                data-toggle="pill"
                                                href={`#pills-${trimKey}`}
                                                role="tab"
                                                aria-controls={`pills-${trimKey}`}
                                            >
                                                {group.label}
                                            </a>
                                        </li>
                                    )}
                                </React.Fragment>
                            );
                        })}
                    </ul>
                    <div style={OVERFLOW} className="tab-content ml-4 w-100" id="pills-tabContent">
                        {configFields.map((group, idx) => {
                            let Component = BaseConfigPlugin;
                            if (group.plugin) {
                                Component = GROUPS[group.plugin];
                            }

                            let trimKey = group.label.replace(/\s/g, '');
                            return (
                                <div
                                    className={`tab-pane fade ${idx == 0 ? 'show active' : ''}`}
                                    role="tabpanel"
                                    aria-labelledby={`pills-${trimKey}-tab`}
                                    id={`pills-${trimKey}`}
                                    key={`tabpanel_${idx}_${trimKey}`}
                                >
                                    <Component fields={group.fields} updateField={updateField} />
                                </div>
                            );
                        })}
                    </div>
                </React.Fragment>
            ) : null}
        </div>
    );
};

document.addEventListener('readystatechange', (event) => {
    if (event.target.readyState === 'interactive') {
        let element = document.querySelector('#serverConfigID');
        if (element) {
            let isExplorer = element.getAttribute('data-isExplorer');
            let isAC = element.getAttribute('data-isAC');

            let isExplorerValue = isExplorer === 'true';
            let isACValue = isAC === 'true';

            ReactDOM.render(<ServerConfigForm isExplorer={isExplorerValue} isAC={isACValue} />, element);
        }
    }
});
