import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { axiosInstance } from 'utils';
import { populateModalDialog, populateErrorModal } from 'swa';

const MigrationsPlugin = () => {
    const userURIsBtn = useRef(null);
    const titleBtn = useRef(null);
    const usersBtn = useRef(null);
    const rulesBtn = useRef(null);

    const userURIsRef = useRef(null);
    const titleRef = useRef(null);
    const usersRef = useRef(null);
    const rulesRef = useRef(null);

    const migrateUserURIs = () => {
        userURIsBtn.current.setAttribute('disabled', 'disabled');
        userURIsRef.current.classList.remove('d-none');
        return axiosInstance
            .get('migrateUserURIs')
            .then((response) => {
                userURIsBtn.current.removeAttribute('disabled', 'disabled');
                userURIsRef.current.classList.add('d-none');
                if (response.data) {
                    populateModalDialog('Migrated ' + response.data.userURIs + ' triples with user URIs.', null, null);
                } else {
                    populateErrorModal('Unexpected result: ' + response.data, null, null);
                }
            })
            .catch((err) => {
                userURIsBtn.current.removeAttribute('disabled', 'disabled');
                userURIsRef.current.classList.add('d-none');
                populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
            });
    };

    const migrateTitles = () => {
        titleBtn.current.setAttribute('disabled', 'disabled');
        titleRef.current.classList.remove('d-none');
        return axiosInstance
            .get('migrateJobTitles')
            .then((response) => {
                titleBtn.current.removeAttribute('disabled', 'disabled');
                titleRef.current.classList.add('d-none');
                if (response.data) {
                    populateModalDialog('Migrated ' + response.data.jobTitles + ' job titles.', null, null);
                } else {
                    populateErrorModal('Unexpected result: ' + response.data, null, null);
                }
            })
            .catch((err) => {
                titleBtn.current.removeAttribute('disabled', 'disabled');
                titleRef.current.classList.add('d-none');
                populateErrorModal('Operation failed: ' + err, 'Operation failed', null);
            });
    };

    const migrateUsers = () => {
        usersBtn.current.setAttribute('disabled', 'disabled');
        usersRef.current.classList.remove('d-none');
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'teamwork:MigrateUsersService',
                },
            })
            .then((response) => {
                usersBtn.current.removeAttribute('disabled', 'disabled');
                usersRef.current.classList.add('d-none');
                let { data } = response;
                let returnStr = '';
                data.forEach((graph) => {
                    returnStr += `Users from  ${graph.users} successfully Migrated to ${graph.userData} <br />`;
                });
                populateModalDialog(returnStr);
            })
            .catch((err) => {
                usersBtn.current.removeAttribute('disabled', 'disabled');
                usersRef.current.classList.add('d-none');
                console.error(err);
            });
    };

    const migrateRules = () => {
        rulesBtn.current.setAttribute('disabled', 'disabled');
        rulesRef.current.classList.remove('d-none');
        axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'teamwork:MigrateURIRulesService',
                },
            })
            .then((response) => {
                rulesBtn.current.removeAttribute('disabled', 'disabled');
                rulesRef.current.classList.add('d-none');
                let { data } = response;
                let returnStr = '';
                data.forEach((graph) => {
                    returnStr += `URIs for graph ${graph.projectGraph} successfully Migrated <br />`;
                });
                populateModalDialog(returnStr);
            })
            .catch((err) => {
                rulesBtn.current.removeAttribute('disabled', 'disabled');
                rulesRef.current.classList.add('d-none');
                console.error(err);
            });
    };

    return (
        <div>
            <div>
                <button ref={rulesBtn} className="btn btn-light" onClick={migrateRules}>
                    <span>Migrate Rules from previous versions</span>
                </button>
                <i ref={rulesRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
            </div>
            <div className="mt-3">
                <button ref={usersBtn} className="btn btn-light" onClick={migrateUsers}>
                    <span>Migrate Users from previous versions</span>
                </button>
                <i ref={usersRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
            </div>
            <div className="mt-3">
                <button ref={titleBtn} className="btn btn-light" onClick={migrateTitles}>
                    <span>Migrate Job Titles from previous versions</span>
                </button>
                <i ref={titleRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
            </div>
            <div className="mt-3">
                <button ref={userURIsBtn} className="btn btn-light" onClick={migrateUserURIs}>
                    <span>Migrate doubly-encoded user URIs from previous versions</span>
                </button>
                <i ref={userURIsRef} className="fas fa-spinner fa-pulse ml-2 d-none"></i>
            </div>
        </div>
    );
};

MigrationsPlugin.propTypes = {
    data: PropTypes.arrayOf(PropTypes.object),
    updateField: PropTypes.func,
};

export default MigrationsPlugin;
